import React, { useEffect, useState } from "react";
import { getCategoryInfoWithCount } from "../../../utils";
import { CountBox } from "./common/count-box";
import { CountBoxWrapper } from "./common/count-box-wrapper";
import { TagetLink } from "./common/link";
import { Name } from "./common/name";
import { NavWrapper } from "./common/nav-wrapper";

export const CategoryNavigator = ({ articles }) => {
  const [loading, setLoading] = useState(true);
  const [categoryInfos, setCategoryInfos] = useState([]);

  useEffect(() => {
    setCategoryInfos(
      getCategoryInfoWithCount(
        articles.map((node) => node.frontmatter.category)
      )
    );
    setLoading(false);
  }, [articles]);

  if (loading) return <></>;

  return (
    <NavWrapper>
      {categoryInfos.map((category, idx) => (
        <TagetLink
          to={category.slug ? `/category/${category.slug}` : "/"}
          key={idx}
        >
          <Name>{category.ko}</Name>
          <CountBoxWrapper>
            <CountBox>{category.count}</CountBox>
          </CountBoxWrapper>
        </TagetLink>
      ))}
    </NavWrapper>
  );
};
