import React from "react";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { ArticleWrapper } from "./wrapper";
import { ArticleContainer } from "./container";
import { BlogLink } from "./link";
import { ArticleInfoWrapper } from "./article-info/wrapper";
import { ArticleDate } from "./article-info/date";
import { ArticleDescription } from "./article-info/description";
import { ArticleTitle } from "./article-info/title";
import { ImageStyles } from "./image/styles";
import { ImageWrapper } from "./image/wrapper";
import { PaginationSection } from "./pagination";

export const ArticleSection = ({ articlesInfoProps }) => {
  const { nodes: articles, pageInfo } = articlesInfoProps;
  return (
    <>
      <ArticleWrapper>
        {articles.map((node) => (
          <ArticleContainer key={node.id}>
            <BlogLink to={`/${node.slug}`}>
              <ImageWrapper>
                <GatsbyImage
                  image={getImage(node.frontmatter.hero_image)}
                  alt={node.frontmatter.hero_image_alt}
                  imgStyle={ImageStyles}
                />
              </ImageWrapper>
              <ArticleInfoWrapper>
                <ArticleTitle>{node.frontmatter.title}</ArticleTitle>
                <ArticleDescription>
                  {node.frontmatter.description}
                </ArticleDescription>
                <ArticleDate>{node.frontmatter.date}</ArticleDate>
              </ArticleInfoWrapper>
            </BlogLink>
          </ArticleContainer>
        ))}
        <PaginationSection pageInfo={pageInfo} />
      </ArticleWrapper>
    </>
  );
};
