import React, { useEffect, useState } from "react";
import { getTagFullInfoDictionary } from "../../../utils";
import { CountBox } from "./common/count-box";
import { CountBoxWrapper } from "./common/count-box-wrapper";
import { TagetLink } from "./common/link";
import { Name } from "./common/name";
import { NavWrapper } from "./common/nav-wrapper";

export const TagNavigator = ({ articles }) => {
  const [loading, setLoading] = useState(true);
  const [tagInfos, setTagInfos] = useState([]);

  useEffect(() => {
    setTagInfos(
      getTagFullInfoDictionary(articles.map((node) => node.frontmatter.tags))
    );
    setLoading(false);
  }, [articles]);

  if (loading) return <></>;

  return (
    <NavWrapper>
      {tagInfos.slice(0, 10).map((tag, idx) => (
        <TagetLink to={`/tag/${tag.slug}`} key={idx}>
          <Name>{tag.ko}</Name>
          <CountBoxWrapper>
            <CountBox>{tag.count}</CountBox>
          </CountBoxWrapper>
        </TagetLink>
      ))}
    </NavWrapper>
  );
};
